  const listOfImages = document.querySelector(".gallery")
  
  function createMarkup(array){
    return array.map(image => `<li class="gallery-item">
    <a class="gallery-link" href="${image.original}">
      <img
        class="gallery-image"
        src="${image.preview}"
        alt="${image.description}"
      />
    </a>
  </li>`
  ).join('')
}  
  
  function rendering(){
  listOfImages.innerHTML = createMarkup(images)
  }
  
  rendering()

